.Meal {
    display: flex;
    border-bottom: 1px #f2f2f2 solid;
    padding: 30rem 20rem;
    align-items: center;
}

.ImgBox {
    width: 220rem;
    transition: transform 1s;
}

.ImgBox:hover {
    transform: scale(1.1);
}

img {
    width: 100%;
}

.Title {
    font-weight: normal;
    font-size: 36rem;
    margin: 0;
}

.Desc {
    color: #BBB;
    font-size: 24rem;
    margin: 0;
    padding-right: 40rem;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
}

.PriceWrap {
    padding-right: 40rem;
    margin-top: 40rem;
    display: flex;
    justify-content: space-between;
}

.Price {
    font-weight: bold;
    font-size: 36rem;
}

.Price::before {
    content: "￥";
    font-size: 24rem;
}